        if (column.label == "Layer" && order == "descending") {
          // this.all.sort(compareFn);
          // const Overall = (a, b) => (String(a.Overall_Risk_determination) > String(b.Overall_Risk_determination) ? 1 : -1);
          // const compareFn = (a, b) =>
          //   Number(a.Layer) > Number(b.Layer) ? 1 : -1;

          this.all.sort(
            (firstItem, secondItem) => firstItem.Layer - secondItem.Layer ||firstItem.Overall_Risk_determination - secondItem.Overall_Risk_determination
          );
        } else if (column.label == "Layer" && order == "ascending") {
          // this.all.reverse(
          //   compareFn
          // );
          const compareFn_h = (a, b) =>
            Number(a.Layer) > Number(b.Layer) ? 1 : -1;

          this.all.reverse(
            (firstItem, secondItem) => firstItem.Layer - secondItem.Layer ||firstItem.Overall_Risk_determination - secondItem.Overall_Risk_determination
          );
        }
      }
    },
    set_number_ORD() {
      if (
        this.list.Impact_rating_Result === "Negligible" ||
        this.list.Attack_feasibility_Result === "Very Low"
      ) {
        this.list.Overall_Risk_determination = 1;
      }
      if (
        this.list.Impact_rating_Result === "Major" &&
        this.list.Attack_feasibility_Result === "Low"
      ) {
        this.list.Overall_Risk_determination = 2;

        // this.and_result = 2;
      }
      if (
        this.list.Impact_rating_Result === "Severe" &&
        this.list.Attack_feasibility_Result === "Low"
      ) {
        this.list.Overall_Risk_determination = 3;
      }
      if (
        this.list.Impact_rating_Result === "Moderate" &&
        this.list.Attack_feasibility_Result === "Medium"
      ) {
        this.list.Overall_Risk_determination = 2;
      }
      if (
        this.list.Impact_rating_Result === "Moderate" &&
        this.list.Attack_feasibility_Result === "Low"
      ) {
        this.list.Overall_Risk_determination = 1;
      }
      if (
        this.list.Impact_rating_Result === "Major" &&
        this.list.Attack_feasibility_Result === "Medium"
      ) {
        this.list.Overall_Risk_determination = 3;

        //  return "color3";
      }
      if (
        this.list.Impact_rating_Result === "Severe" &&
        this.list.Attack_feasibility_Result === "Medium"
      ) {
        this.list.Overall_Risk_determination = 4;

        // return "color4";
      }
      if (
        this.list.Impact_rating_Result === "Moderate" &&
        this.list.Attack_feasibility_Result === "High"
      ) {
        this.list.Overall_Risk_determination = 3;

        //   return "color3";
      }
      if (
        this.list.Impact_rating_Result === "Major" &&
        this.list.Attack_feasibility_Result === "High"
      ) {
        this.list.Overall_Risk_determination = 4;

        //return "color4";
      }
      if (
        this.list.Impact_rating_Result === "Severe" &&
        this.list.Attack_feasibility_Result === "High"
      ) {
        this.list.Overall_Risk_determination = 5;

        // return "color5";
      }
    },
    determination_color({ row, column, rowIndex, columnIndex }) {
      if (column.property === "Overall_Risk_determination") {
        if (
          row.Impact_rating_Result === "Negligible" ||
          row.Attack_feasibility_Result === "Very Low"
        ) {
          return "color1";
        }
        if (
          row.Impact_rating_Result === "Major" &&
          row.Attack_feasibility_Result === "Low"
        ) {
          return "color2";

          // this.and_result = 2;
        }
        if (
          row.Impact_rating_Result === "Severe" &&
          row.Attack_feasibility_Result === "Low"
        ) {
          return "color3";
        }
        if (
          row.Impact_rating_Result === "Moderate" &&
          row.Attack_feasibility_Result === "Medium"
        ) {
          return "color2";
        }
        if (
          row.Impact_rating_Result === "Moderate" &&
          row.Attack_feasibility_Result === "Low"
        ) {
          return "color1";
        }
        if (
          row.Impact_rating_Result === "Major" &&
          row.Attack_feasibility_Result === "Medium"
        ) {
          return "color3";
        }
        if (
          row.Impact_rating_Result === "Severe" &&
          row.Attack_feasibility_Result === "Medium"
        ) {
          return "color4";
        }
        if (
          row.Impact_rating_Result === "Moderate" &&
          row.Attack_feasibility_Result === "High"
        ) {
          return "color3";
        }
        if (
          row.Impact_rating_Result === "Major" &&
          row.Attack_feasibility_Result === "High"
        ) {
          return "color4";
        }
        if (
          row.Impact_rating_Result === "Severe" &&
          row.Attack_feasibility_Result === "High"
        ) {
          return "color5";
        }
      }
    },

    async reg() {
      this.loading_rge = true;
      this.set_number_ORD();
      var today = new Date();
      const hours = today.getHours();
      const minutes = today.getMinutes();
      const seconds = today.getSeconds();
      var date =
        today.getFullYear() +
        "-" +
        (today.getMonth() + 1) +
        "-" +
        today.getDate() +
        "-" +
        today.getHours() +
        "-" +
        today.getMinutes() +
        "-" +
        today.getSeconds();
      const docRef = await addDoc(collection(db, "ThreatModeling"), {
        Work: this.list.Work,
        Layer: this.list.Layer,
        AttackEntryPoint: this.list.AttackEntryPoint,
        Impact_S: this.list.Impact_S,
        Impact_O: this.list.Impact_O,
        TargetedAccuracy: this.list.TargetedAccuracy,
        Impact_rating_Result: this.list.Impact_rating_Result,
        Knowledge: this.list.Knowledge,
        Equipment: this.list.Equipment,
        Window_of_Opportunity: this.list.Window_of_Opportunity,
        SpecialistExpertise: this.list.SpecialistExpertise,
        Attack_feasibility_Result: this.list.Attack_feasibility_Result,
        Overall_Risk_determination: this.list.Overall_Risk_determination,
        data: date,
        // allresult: "",
        // statusresult: "",
        // name: "",
        // ipaddress: "",
      });
      this.aza();
      this.list = {
        Work: "",
        Layer: "",
        AttackEntryPoint: "",
        Impact_S: "",
        Impact_O: "",
        TargetedAccuracy: "",
        Impact_rating_Result: "",
        Knowledge: "",
        Equipment: "",
        Window_of_Opportunity: "",
        SpecialistExpertise: "",
        Attack_feasibility_Result: "",
        Overall_Risk_determination: "",
        data: "",
      };
      this.loading_rge = false;
    },

    async aza() {
      const q = query(collection(db, "ThreatModeling"), orderBy("data"));
      const querySnapshot = await getDocs(q);

      //  const querySnapshot = await getDocs(collection(db, "ThreatModeling"));
      this.all = [];
      var Id = 1;
      querySnapshot.forEach((doc) => {
        this.all.push({ id: Id, ...doc.data() });
        Id++;
      });
      this.all.reverse(
            (firstItem, secondItem) => firstItem.data - secondItem.data
          );
      //this.all.reverse();
    },
    btn_reg(item) {
      this.edit = item.groupsIdPk;
    },
    change() {
      if (
        this.list.Impact_S.length > 0 &&
        this.list.Impact_O.length > 0 &&
        this.list.TargetedAccuracy.length > 0
      ) {
        this.result =
          parseInt(this.list.Impact_S) +
          parseInt(this.list.Impact_O) +
          parseInt(this.list.TargetedAccuracy);
        if (6 < this.result < 10) {
          this.list.Impact_rating_Result = "Severe";
        }
        if (this.result === 1 || this.result === 2) {
          this.list.Impact_rating_Result = "Negligible";
        }

        if (this.result === 3 || this.result === 4) {
          this.list.Impact_rating_Result = "Moderate";
        }
        if (this.result === 5 || this.result === 6) {
          this.list.Impact_rating_Result = "Major";
        }
      }
    },
    change_attack_feasibility() {
      if (
        this.list.Knowledge.length > 0 &&
        this.list.Equipment.length > 0 &&
        this.list.Window_of_Opportunity.length > 0 &&
        this.list.SpecialistExpertise.length > 0
      ) {
        const W_O = this.list.Window_of_Opportunity.split("_");
        this.result_attack_feasibility =
          parseInt(this.list.Knowledge) +
          parseInt(this.list.Equipment) +
          parseInt(W_O[1]) +
          parseInt(this.list.SpecialistExpertise);
        if (
          this.result_attack_feasibility >= 0 &&
          this.result_attack_feasibility <= 13
        ) {
          this.list.Attack_feasibility_Result = "High";
        }
        if (
          this.result_attack_feasibility >= 14 &&
          this.result_attack_feasibility <= 19
        ) {
          this.list.Attack_feasibility_Result = "Medium";
        }
        if (
          this.result_attack_feasibility >= 20 &&
          this.result_attack_feasibility <= 24
        ) {
          this.list.Attack_feasibility_Result = "Low";
        }
        if (
          this.result_attack_feasibility >= 25 &&
          this.result_attack_feasibility <= 38
        ) {
          this.list.Attack_feasibility_Result = "Very Low";
        }
      }
    },
  },
};
</script>
  
