<template>
  <el-table
    :data="list"
    style="width: 100%"
    :cell-class-name="determination_color"
  >
    <el-table-column label="  ">
      <el-table-column prop="Impact" label="Impact"> </el-table-column>
      <el-table-column prop="Impact_n" label="Impact_n"> </el-table-column>
    </el-table-column>
    <el-table-column label="Feasibility">
      <el-table-column width="80" prop="VeryLow" label="Very Low">
      </el-table-column>
      <el-table-column width="80" prop="Low" label="Low"> </el-table-column>
      <el-table-column width="80" prop="Medium" label="Medium">
      </el-table-column>
      <el-table-column width="80" prop="High" label="High"> </el-table-column>
    </el-table-column>
  </el-table>
</template>
  <script>
export default {
  data() {
    return {
      list: [
        {
          Impact: "  ",
          Impact_n: "  ",
          VeryLow: "1",
          Low: "2",
          Medium: "3",
          High: "4",
          color: "",
        },
        {
          Impact: "Severe",
          Impact_n: "4",
          VeryLow: "1",
          Low: "3",
          Medium: "4",
          High: "5",
          color: "",
        },
        {
          Impact: "Major",
          Impact_n: "3",
          VeryLow: "1",
          Low: "2",
          Medium: "3",
          High: "4",
          color: "",
        },
        {
          Impact: "Moderate",
          Impact_n: "2",
          VeryLow: "1",
          Low: "1",
          Medium: "2",
          High: "3",
          color: "",
        },
        {
          Impact: "Negligible",
          Impact_n: "1",
          VeryLow: "1",
          Low: "1",
          Medium: "1",
          High: "1",
          color: "",
        },
      ],
    };
  },
  methods: {
    determination_color({ row, column, rowIndex, columnIndex }) {
      if (column.property === "VeryLow" && rowIndex == 1) {
        return "color1";
      }
      if (column.property === "VeryLow" && rowIndex == 2) {
        return "color1";
      }
      if (column.property === "VeryLow" && rowIndex == 3) {
        return "color1";
      }
      if (column.property === "VeryLow" && rowIndex == 4) {
        return "color1";
      }
      if (column.property === "Low" && rowIndex == 1) {
        return "color3";
      }
      if (column.property === "Low" && rowIndex == 2) {
        return "color2";
      }
      if (column.property === "Low" && rowIndex == 3) {
        return "color1";
      }
      if (column.property === "Low" && rowIndex == 4) {
        return "color1";
      }
      if (column.property === "Medium" && rowIndex == 1) {
        return "color4";
      }
      if (column.property === "Medium" && rowIndex == 2) {
        return "color3";
      }
      if (column.property === "Medium" && rowIndex == 3) {
        return "color2";
      }
      if (column.property === "Medium" && rowIndex == 4) {
        return "color1";
      }
      if (column.property === "High" && rowIndex == 1) {
        return "color5";
      }
      if (column.property === "High" && rowIndex == 2) {
        return "color4";
      }
      if (column.property === "High" && rowIndex == 3) {
        return "color3";
      }
      if (column.property === "High" && rowIndex == 4) {
        return "color1";
      }
    },
  },
};
</script>
  
  <style>
</style>